import React, { Component } from "react";

import { Bar, Pie, Line } from "react-chartjs-2";
import Chart from "react-google-charts";
import Nav from "./components/nav";
import Bin from "./components/bin";
import Trash from "./components/trash";

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      user: [],
      bins: [],
      displayBin: {
        id: -1,
        name: "",
        location: "",
        containers: [
          { id: 0, trash: 0, data_timestamp: [] },
          { id: 0, trash: 0, data_timestamp: [] },
          { id: 0, trash: 0, data_timestamp: [] },
        ],
        trashes: [
          ["bottle", 100],
          ["plasticBag", 100],
          ["can", 100],
        ],
      },
    };
    this.fetchData();
  }

  fetchData() {
    // fetch data from server
    const data = fetch("/bins")
      .then((res) => res.json())
      .then((result) => this.setState({ bins: result }));
  }

  getChartData() {
    return {
      labels: [
        "8:00 30/4/2021",
        "14:00 30/4/2021",
        "20:00 30/4/2021",
        "2:00 1/5/2021",
        "8:00 1/5/2021",
        "14:00 1/5/2021",
      ],
      datasets: [
        {
          label: "Container 1",
          data: this.state.displayBin.containers[0].data_timestamp,
          borderColor: "lightsalmon",
          backgroundColor: "lightsalmon",
        },
        {
          label: "Container 2",
          data: this.state.displayBin.containers[1].data_timestamp,
          borderColor: "yellowgreen",
          backgroundColor: "yellowgreen",
        },
        {
          label: "Container 3",
          data: this.state.displayBin.containers[2].data_timestamp,
          borderColor: "lightseagreen",
          backgroundColor: "lightseagreen",
        },
      ],
      options: {
        line: {
          tension: 0,
        },
      },
    };
  }

  clickHandler = (binId) => {
    const bin = this.state.bins.filter((bin) => bin.id === binId);
    this.setState({ displayBin: bin[0] });
  };

  render() {
    if (this.state.bins.length !== 0 && this.state.displayBin.id === -1)
      this.state.displayBin = this.state.bins[0];

    var chartData = this.getChartData();
    
    return (
      <React.Fragment>
        <Nav />
        <div className="content">
          <div className="grid" id="col1">
            <div id="bins">
              {this.state.bins.map((bin) => (
                <Bin bin={bin} onClick={this.clickHandler} />
              ))}
            </div>
          </div>

          <div className="grid" id="col2">
            <div id="containers">
              {/* Donut Chart */}
              <div className="title">Container</div>
              <div className="piechart">
                {this.state.displayBin.containers.map((container) => (
                  <div>
                    <a
                      id="piechartStat"
                      style={{
                        color: container.trash > 50 ? "lightcoral" : "#20B2AA",
                      }}
                    >
                      {container.trash.toString() + "%"}
                    </a>
                    <Chart
                      style={{ marginTop: "10px" }}
                      width={"310px"}
                      height={"300px"}
                      chartType="PieChart"
                      data={[
                        ["", ""],
                        ["Trash", container.trash],
                        ["Space", 100 - container.trash],
                      ]}
                      options={{
                        title: "Container " + container.id,
                        pieHole: 0.8,
                        colors: ["#F6546A", "#20B2AA"],
                      }}
                    />
                  </div>
                ))}
              </div>

              {/* Trash */}
              <div className="title">Trash</div>
              <div className="trashes">
                {this.state.displayBin.trashes.map((trash) => (
                  <Trash trash={trash} />
                ))}
              </div>

              {/* Line Chart */}
              <div className="title">Chart</div>
              <Bar data={chartData}></Bar>
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default App;
